import EL from './elements';
var Thumbs = (function () {
    function Thumbs(onChangeImage) {
        var _this = this;
        this.presets = [
            document.getElementById('preset0'),
            document.getElementById('preset1'),
            document.getElementById('preset2'),
            document.getElementById('preset3'),
            document.getElementById('preset4'),
            document.getElementById('preset5'),
            document.getElementById('preset6'),
        ];
        this.outerElement = document.getElementById('thumbs');
        this.setClickHandlers = function () {
            _this.presets.forEach(function (preset) {
                preset.onclick = function () {
                    EL.imgElement.src = preset.src;
                    setTimeout(function () {
                        _this.onChangeImage();
                    }, 100);
                };
            });
        };
        this.addPreset = function () {
            var file = EL.fileInput.files[0];
            if (!file)
                return;
            var newPreset = document.createElement('img');
            newPreset.src = EL.imgElement.src;
            newPreset.className = 'preset';
            _this.outerElement.appendChild(newPreset);
            _this.presets.push(newPreset);
            _this.setClickHandlers();
        };
        this.onChangeImage = onChangeImage;
        this.setClickHandlers();
    }
    return Thumbs;
}());
export default Thumbs;
